/* Slider on flex-box */
@import url('https://fonts.googleapis.com/css?family=Bad+Script&display=swap');

.Block_Flex {
	left: 260px;
	position: absolute;
}

.slider {
	display: flex;
	justify-content: space-between;
	width: 650px;
	position: absolute;
}

.item {
	height: 100vh;
	display: flex;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
	background-color: #aeaeae;
	flex-basis: 40%;
	transition: 0.6s;
	border: solid 0.25px #000;
}

.item span {
	font-size: 2rem;
	text-shadow: 0px 0px 10px #000;
	font-family: 'Bad Script', cursive;
}

.item:hover {
		transition: 0.6s;
		flex-basis: 100%;
		font-size: 46px;
		cursor: default;
}
