* {
	box-sizing: border-box;
	outline: none;
}


.ConnectsField {
	position: absolute;
	right: 230px;
	overflow: hidden;
	width: 270px;
	height: 80px;
	border: 2px solid #fff;
	background: rgba(260, 260, 260, .2);
	top: 55px;
	font-size: 32px;
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	transition: .4s;
} 


.ConnectsField:hover {
	background: rgba(260, 260, 260, .8);
	transition: .4s;
	color: #282923;
}

/* Open and Hidden */

#hiddenCon {
	top: 10%;
	width: 30%;
	height: 500px;
	background-color: rgba(149, 149, 149, .2);
	border: 2px solid #fff;
	opacity: 1;
	display: none;
	position: absolute;
	left: 15%;
	z-index: 9;
	transition: .4s;
}

.HideBlockWithForm {
	width: 170px;
	height: 45px;
	margin: 55px;
	left: 175px;
	position: absolute;
	bottom: 0px;
	font-size: 26px;
	font-family: initial;
	color: #fff;
	border: 2px solid #FFF;
	background-color: rgba(149, 149, 149, .2);
	display: none;
}

#SendFormName {
	width: 91%;
	margin: 30px 20px;
	height: 35px;
	font-size: 16px;
	padding: 5px;
	color: #fff;
	border: 2px solid #fff;
	background-color: rgba(260, 260, 260, .1);
}

#SendFormPhone {
	width: 91%;
	margin: 35px 20px;
	height: 35px;
	font-size: 16px;
	padding: 5px;
	position: absolute;
	top: 110px;
	left: 0px;
	color: #fff;
	border: 2px solid #fff;
	background-color: rgba(260, 260, 260, .1);
}


#SendFormName::focus {
	background-color: rgba(260, 260, 260, .1);
}

#SendFormTheme {
	width: 91%;
	margin-left: 20px;
	margin-bottom: 75px;
	height: 35px;
	font-size: 16px;
	padding: 5px;
	margin-top: -25px;
	color: #fff;
	border: 2px solid #fff;
	background-color: rgba(260, 260, 260, .1);
}

#FormArea {
	width: 91%;
	height: 230px;
	margin-left: 20px;
	font-size: 16px;
	padding: 10px 5px;
	color: #fff;
	border: 2px solid #fff;
	background-color: rgba(260, 260, 260, .1);
	font-weight: lighter;
	resize: none;
}

#FormArea::-webkit-scrollbar {
	width: 2px;
	color: #fff;
}

#FormArea::-webkit-scrollbar-thumb {
	border: 2px solid #fff;
    background-color: #fff;
}

#SendFormTheme::placeholder,
#SendFormName::placeholder,
#FormArea::placeholder,
#SendFormPhone::placeholder {
	color: #eee;
	background-color: rgba(149, 149, 149, .2);
}

#hiddenCon > form > button {
	width: 170px;
	height: 45px;
	margin: 55px;
	left: -35px;
	position: absolute;
	bottom: -49px;
	font-size: 26px;
	font-family: initial;
	color: #fff;
	border: 2px solid #FFF;
	background-color: rgba(149, 149, 149, .7);
	display: none;
}

/* FOR MESSAGE */

#SendArea {
	width: 45%;
	height: 500px;
	background-color: rgba(149, 149, 149, .2);
	border: 2px solid #fff;
	position: absolute;
	right: 125px;
	display: none;
	top: 10%;
}

#BlockWithMessage {
	width: 95%;
	height: 150px;
	border: 2px solid #fff;
	background-color: rgba(149, 149, 149, .7);
	margin: 5% 2.5%;
	color: #fff;
	padding: 10px 15px;
	transition: .4s;
	cursor: default;
}

#titleMessage {
	color: #fff	;
	font-size: 26px;
	width: 100%;
	height: 35px;
	text-align: left;
	padding-left: 10px;
	cursor: default;
}

#Name {
	font-size: 18px;
	color: #fff;
	text-align: left;
	padding-left: 10px;
	height: 35px;
	cursor: default; 
}

#Name:before {
	width: 2px; 
	height: 55px;
	background-color: #fff;
	content: '';
	position: absolute;
	left: 30px;
	top: 55px;
}

#message {
	box-sizing: border-box;
	width: 610px;
	height: 65px;
	overflow: scroll;
	padding-left: 10px;
	display: flex;
	word-break: break-all; 
	font-size: 16px;
	cursor: default;
}

#BlockWithMessage:hover {
	height: 390px;
	transition: .5s;
	background-color: rgba(149, 149, 149, .9);
}

#BlockWithMessage:hover > #message {
	height: 290px;
	transition: .5s;
}

#message::-webkit-scrollbar{
    width:1px;
    color: #ddd;
    display: none;
}

#message::-webkit-scrollbar-thumb{
    border-width: 1px 1px 1px 2px;
    border-color: #555;
    background-color: #777;
    display: none;
}
