@import url('https://fonts.googleapis.com/css?family=Acme&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');
/* FONTS*/
/* DEFAULT SETTINGS */
*,
:before,
:after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/* ANIMATION */
@-webkit-keyframes flowUp {
  from {
    top: -20px;
    opacity: 0;
  }
  to {
    top: -100px;
    opacity: 1;
  }
}
@keyframes flowUp {
  from {
    top: -20px;
    opacity: 0;
  }
  to {
    top: -100px;
    opacity: 1;
  }
}
@-webkit-keyframes cursor {
  from {
    top: 65px;
  }
  to {
    top: 80px;
  }
}
@keyframes cursor {
  from {
    top: 65px;
  }
  to {
    top: 80px;
  }
}
/* SECTION  FOR WRAPPERS */
.wrapper {
  margin: 0 auto;
  width: 1536px;
  background: url(bg.jpg);
  background-size: 100%;
  background-repeat: no-repeat;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
}
.inner-wrapper {
  background: rgba(40, 41, 35, 0.4);
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 2;
}
/* END */
/* SECTION ABOUT */
