  color: #fff;
  position: relative;
  width: 85px;
  text-align: center;
  cursor: pointer;
  line-height: 65px;
  z-index: 2;
}
#skill {
  width: 100px;
  height: 65px;
  border: 2px solid #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: rgba(202, 202, 202, 0.1);
  z-index: 1;
}
.skillReturn {
  width: 100px;
  height: 65px;
  border: 2px solid #fff;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: rgba(202, 202, 202, 0.1);
  z-index: 2;
}
.skill:hover {
  background-color: rgba(202, 202, 202, 0.5);
}
.skills {
  width: 100%;
  height: 65px;
  position: absolute;
  top: 65%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  z-index: 1;
  -webkit-transition: .7s;
  transition: .7s;
}
.MySkills {
  margin-top: 20px;
  font-size: 21px;
  width: 300px;
  height: 350px;
  color: #fff;
  font-family: 'Acme', sans-serif;
  position: absolute;
  left: 550px;
  top: 55px;
}
.TextWithSkill:hover ~ #pic {
  z-index: 1;
  -webkit-animation: flowUp .4s;
          animation: flowUp .4s;
  top: -100px;
  opacity: 1;
}
#pic {
  width: 95px;
  height: 95px;
  position: absolute;
  z-index: -2;
  opacity: 0;
  top: 0px;
  -webkit-transition: .8s;
  transition: .8s;
  padding: 10px 5px;
  border: 2px solid #fff;
  border-radius: 15px;
  background-color: rgba(149, 149, 149, 0.8);
  -webkit-transition: .3s;
  transition: .3s;
}
/* END OF SECTION WITH SCILLS */
/* FOR DOWN ARROW */
.Cursor:after {
  position: absolute;
  width: 3px;
  height: 35px;
  background-color: #fff;
  content: '';
  left: 690px;
  -webkit-transform: rotateZ(-35deg);
          transform: rotateZ(-35deg);
  border-radius: 50px;
  top: 75px;
}
.Cursor:before {
  position: absolute;
  width: 3px;
  height: 35px;
  background-color: #fff;
  content: '';
  left: 709px;
  -webkit-transform: rotateZ(35deg);
          transform: rotateZ(35deg);
  border-radius: 50px;
  top: 75px;
}
.flow {
  width: 55px;
  height: 55px;
  margin-top: -80px;
  position: relative;
  -webkit-animation: cursor .9s infinite;
          animation: cursor .9s infinite;
}
/* CLASSES FOR HIDE AND SHOW ELEMENTS */
#Hidden_Block {
  width: 550px;
  height: 450px;
  background-color: rgba(149, 149, 149, 0.8);
  border: 2px solid #fff;
  opacity: 0;
  display: none;
  position: absolute;
  top: -320px;
  z-index: -2;
  -webkit-transition: .4s;
  transition: .4s;
}
.Show_Block {
  width: 70%;
  height: 500px;
  background-color: rgba(149, 149, 149, 0.7);
  border: 2px solid #fff;
  opacity: 1;
  display: block;
  position: absolute;
  top: -320px;
  left: 15%;
  z-index: 9;
  -webkit-transition: .4s;
  transition: .4s;
}
.Hide_Block {
  display: none;
}
.moveLeft {
  opacity: 0;
  -webkit-transition: .7s;
  transition: .7s;
}
/* END DOWN ARROW */
