  top: 60px;
  left: 5%;
}
section {
  margin-top: 25px;
  margin-left: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
section > div {
  position: absolute;
  right: 0px;
  top: 90px;
  color: #fff;
  font-size: 21px;
  font-family: sans-serif;
  width: 45%;
  height: 350px;
}
section > div > p {
  position: absolute;
  top: 55px;
}
section > div:before {
  content: '';
  width: 2px;
  height: 350px;
  background-color: #fff;
  position: absolute;
  left: -20px;
}
section > div > p > a {
  font-size: 21px;
  color: #fff;
  text-decoration: none;
  position: absolute;
  top: 0px;
  left: 180px;
}
section > div > p > a:hover {
  color: #555;
  -webkit-transition: .4s;
  transition: .4s;
}
section > div > p > a:hover::after {
  content: '';
  width: 60%;
  height: 2px;
  background-color: #555;
  position: absolute;
  bottom: -4px;
  left: 20%;
  -webkit-transition: .4s;
  transition: .4s;
}
.ForLink:before {
  content: '';
  width: 400px;
  height: 2px;
  position: absolute;
  z-index: 9999;
  background-color: #fff;
  top: -100px;
  left: -20px;
}
/* END COMMON SECTION WITH COMMON ELEMENTS */
