

/*Calculator*/

.FieldForGoods {
	width: 85%;
	position: absolute;
	left: 260px;
	height: 350px;
	display: flex;
	flex-wrap: wrap;
}

.Inner_FieldForGoods {
	width: 60%;
	height: 350px;
	display: inherit;
	flex-wrap: wrap;
	position: absolute;
	right: 0;
}

.item1, .item2, .item3, .item4 {
	width: 180px;
	height: 160px;
}

.itemForCalc {
	border: 2px solid #fff;
	background-color: rgba(260, 260, 260, .2);
	padding: 2px 7px;
}

.BlockForGood {
	margin: 5px;
	width: 180px;
	height: 160px;
	position: relative;
}

.forCharacteristics {
	position: absolute;
	display: none;
	width: 180px;
	height: 160px;
	padding: 5px 10px;
	color: #282C2F;
	text-shadow: 0px 0px 2px #eee;
	cursor: pointer;
}

.forCharacteristics > p {
	padding-bottom: 10px;
}

.BlockForGood:hover > .forCharacteristics {
	display: block;
	font-size: 12px;
	background-color: rgba(260, 260, 260, .6);

}

#Name1, #Name2, #Name3, #Name4  {
	font-size: 10px;
	text-decoration: underline;
}

#purchase1, #discount1, #purchase2, #discount2, 
#purchase3, #discount3, #purchase4, #discount4, {
	font-size: 13px;
}

.calculator {
	position: absolute;
	left: -4px;
	width: 355px;
	height: 350px;
	border: 2px solid #fff;
	padding: 20px 10px;
	background-color: rgba(260, 260, 260, .2);
}

.BlockForCalc {
	margin-bottom: 25px;
}

.answerCalc {
	width: 100%;
	height: 40px;
	border-top: 2px solid #fff;
	position: absolute;
	bottom: 0px;
	left: 0px;
	padding: 5px 10px;
	line-height: 28px;
}