@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bad+Script&display=swap);
/* FONTS*/
/* DEFAULT SETTINGS */
*,
:before,
:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* ANIMATION */
@-webkit-keyframes flowUp {
  from {
    top: -20px;
    opacity: 0;
  }
  to {
    top: -100px;
    opacity: 1;
  }
}
@keyframes flowUp {
  from {
    top: -20px;
    opacity: 0;
  }
  to {
    top: -100px;
    opacity: 1;
  }
}
@-webkit-keyframes cursor {
  from {
    top: 65px;
  }
  to {
    top: 80px;
  }
}
@keyframes cursor {
  from {
    top: 65px;
  }
  to {
    top: 80px;
  }
}
/* SECTION  FOR WRAPPERS */
.wrapper {
  margin: 0 auto;
