	font-size: 18px;
	color: #fff;
	text-align: left;
	padding-left: 10px;
	height: 35px;
	cursor: default; 
}

#Name:before {
	width: 2px; 
	height: 55px;
	background-color: #fff;
	content: '';
	position: absolute;
	left: 30px;
	top: 55px;
}

#message {
	box-sizing: border-box;
	width: 610px;
	height: 65px;
	overflow: scroll;
	padding-left: 10px;
	display: flex;
	word-break: break-all; 
	font-size: 16px;
	cursor: default;
}

#BlockWithMessage:hover {
	height: 390px;
	-webkit-transition: .5s;
	transition: .5s;
	background-color: rgba(149, 149, 149, .9);
}

#BlockWithMessage:hover > #message {
	height: 290px;
	-webkit-transition: .5s;
	transition: .5s;
}

#message::-webkit-scrollbar{
    width:1px;
    color: #ddd;
    display: none;
}

#message::-webkit-scrollbar-thumb{
    border-width: 1px 1px 1px 2px;
    border-color: #555;
    background-color: #777;
    display: none;
}

